.Searchbar{
	display: flex;
	justify-content: center;
	width: 500px;
	box-shadow: 0 25px 50px -1px rgba(0, 0, 0, 0.5);
	border:2px solid white;
	border-radius: 50px;
	overflow:hidden;
	height: 40px;
}
.Searchbar input,.Searchbar button{
	outline:none;
	border:none;
}
.SearchInput{
	width: 100%;
}
.Searchbar button{
	 background-color: #ffa1c5;
	 color: white;
}